/* box-section */

.shop-section {
    display: flex;
    height: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color:rgb(30, 30, 30);;
}
.box1.box.margin,
.box2.box.margin,
.box3.box.margin,
.box4.box.margin{
    position: relative;
    top: 1rem;
}
.box {
    height: 400px;
    width: 23%;
    background-color: rgb(30, 30, 30);
    padding: 20px 0px 15px;
    margin-top: 15px;
}

.hover:hover{
    cursor: pointer;
    background-color: green;
}
p.hover{
    margin-left: 4rem;
    margin-top: 5px;
    text-align: center;
    font-size: 2px;
    border-style: inset;
}
.box-content {
    margin-left: 1rem;
    margin-right: 1rem;
}
.box-content p {
    color: red;
   font-size: 1.5rem;
}
.ProjectTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: -10rem;
    margin-bottom: 20%;
   margin-left: 45%;
   -webkit-text-stroke:  1px green;
   animation: head 5s ease-in-out infinite;
   transition-duration: 1sec;
}
@keyframes head {
    25%{
        -webkit-text-stroke:1px green;
    }
    50%{
        -webkit-text-stroke:1px yellow;
    }
    75%{
        -webkit-text-stroke:1px aqua;
    }
    100%{
        -webkit-text-stroke:1px red;
    }
}
@keyframes yup {
    25%{
        text-shadow: 2px 2px 5px red;
    }
    50%{
        text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
    }
    75%{
        color: white;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    }
    100%{
        text-shadow: 2px 2px;
    }
}
.box-content>h2{
    -webkit-text-stroke:  1px green;
    animation: yup 3s ease-in infinite;
    transition-duration: 1sec;
}
.box-img{
    height: 20vw;
    width: 15vw;
    background-size: cover;
    margin-top: 1rem;
    margin-bottom: 1rem;
    /* min-height: calc(100vh - 4rem); */
    width: 100vw;
    max-width: 60rem;
    padding: 0 1rem;
} 
img.img---1 {
    height: 300px;
    width: 20vw;
}
@media screen and (max-width:915px) {
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        }
        .box-img{
            height: 700px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
        }
        .ProjectTitle{
            margin-left: 19%;
        }
        .parahead{
            text-align: center;
            font-weight: 700;
           font-size: 2rem;
        }
        p.box01{
            font-size: 3rem;
            text-align: center;
            margin-top: 1rem;
            margin-right: 3rem;
        }
        img.img---1 {
            height: 700px;
            width: 90vw;
        }
}
