.navbar {
  /* background: rgb(30, 30, 30); */
  background-color: #000;
  height: 6rem;
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 3;
}

.logo {
  margin-bottom: 0.5rem;
  object-fit: cover;
  height: 6rem;
  width: 5rem;
}

.desktopMenuListItem {
  margin: 1rem;
  cursor: pointer;
}
.desktopMenuListItem:hover {
  color: yellow;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid yellow;
  -webkit-text-stroke: 0.4px green;
  animation: killer 0.4s ease-in-out infinite;
  transition-duration: 1sec;
}

@keyframes killer {
  25% {
    color: green;
  }
  50% {
    color: red;
  }
  75% {
    color: aqua;
  }
  100% {
    color: yellow;
  }
}

.desktopMenuBtn {
  background: linear-gradient(120deg, red, yellow);
  color: #000;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
  text-shadow: 2px 2px 4px green;
  cursor: pointer;
}
.desktopMenuImg {
  object-fit: cover;
  height: 1rem;
  width: 1rem;
  margin: 1rem;
}
.desktopMenuImg:hover {
  cursor: pointer;
}
.ContactText:hover {
  font-family: cursie;
}
.active{
  color: yellow;
  animation: killer 0.4s ease-in-out infinite;
  transition-duration: 2sec;
  border-bottom: 3px solid yellow;
  padding-bottom: 0.5rem;
}
.mobMenu{
  display: none;
  object-fit: cover;
  height: 1.8rem;
}

@keyframes navactive{
  25%{
    color: green;
  }
  50%{
    color: yellow;
  }
  75%{
    color: aqua;
  }
  100%{
    color: chocolate;
    background: linear-gradient(120deg,blue,aqua);
  }
}

.navMenu{
  position: absolute;
  top: 4rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  height: fit-content;
  min-width: 15rem;
  background: rgb(40, 40, 40);
  border-radius: 1rem;
}
.listItem{
  color: white;
  padding: 0.5rem 3rem;
  margin: 0.25rem;
  background: rgb(30, 30, 30);
}
@media screen and (max-width:720px) {
  .mobMenu{
    display: flex;
  }
  .desktopMenu{
    display: none;
  }
  .desktopMenuBtn{
    display: none;
  }
}
