*{
    font-family: SF PRO DISPLAY;
}
#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.introContent{
    height: 100vh;
    width: 100vh;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName{
    color: yellow;
    -webkit-text-stroke: 0.4px aqua;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btn{
    background:  linear-gradient(20deg, green, aqua);
    font-size: 1.5rem;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    text-align: center;
    cursor: pointer;
    font-weight: 700;
    text-shadow: 3px 2px 5px red;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width:840px) {
     .bg{
        right: -10vw;
     }
     .introContent{
        font-size: 10vw;
     }
     .introPara{
        width: 50%;
        font-size: 0.9rem;
     }
     .hello{
        font-size: 4.5vw;
     }
}
@media screen and (max-width:915px) {
     .bg{
        right: -20vw;
     }
     .btnMob{
        background-image: 'assets/image 1.png';
     }
     
}